.HomePage {
  position: relative;
}
.contact-block {
  display: flex;
  justify-content: center;
  align-items: center;
}
/*content*/
.hero-image {
  overflow: hidden;
}
.mainImg {
  width: 100vw;
}
.phoneImg {
  display: none;
}
.parallax-el {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: url(../../assets/dynastytxt.png) no-repeat 50% 50%;
  background-size: cover;
  top: 20%;
  opacity: 0.8;
}

@media screen and (max-width: 864px) {
  .phoneImg {
    display: block;
    width: 100vw;
  }
  .mainImg {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  .parallax-el {
    top: -20%;
  }
}
/*content*/

.wordblock {
  position: relative;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  width: 40vw;
  height: 40vw;
  padding: 1vw 5vw 1vw 5vw;
  align-items: center;
}

.words {
  width: 100vw;
  background-color: whitesmoke;
  display: flex;
  text-align: center;
  justify-content: space-around;
  padding: 1vw 5vw 1vw 5vw;
  padding-top: 15vh;
}
.btnbox {
  width: 100vw;
  background-color: whitesmoke;
  display: flex;

  justify-content: space-around;
  padding-bottom: 15vh;
}
/*share btn*/
.share-button {
  width: 280px;
  height: 80px;
  background: rgb(115, 205, 228);
  border-radius: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-self: center;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: 0.3s linear;
  z-index: 1000;
  text-decoration: none;
  color: white;
}
.share-button:hover {
  transform: scale(1.1);
  background: rgb(109, 201, 224);
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: 0.3s linear;
  z-index: 1000;
  text-decoration: none;
  color: rgb(224, 224, 224);
}
/*HomeBolck3*/
.content {
  position: relative;
  padding-left: 8vw;
  padding-right: 8vw;
  padding-top: 0vh;

  background-color: rgb(247, 247, 247);
  display: flex;
  flex-direction: row;
  padding-bottom: 0vh;
  justify-content: space-evenly;
}
.wordblock {
  position: relative;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  width: 40vw;
  height: 40vw;
  padding: 1vw 5vw 1vw 5vw;
  align-items: center;
}
.stayc {
  padding-top: 20vh;
}
.imgblock {
  position: relative;
  background: url(../../assets/paintingtree-min.png) no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  width: 40vw;
  height: 40vw;
  padding: 1vw 5vw 1vw 5vw;
}
.connections {
  display: flex;
  flex-direction: row;
}
.halflink {
  display: flex;
  flex-direction: column;
}
.contactmobilefit {
  justify-content: center;
}
@media screen and (max-width: 864px) {
  .content {
    padding-left: 0px;
    padding-right: 0px;
    flex-direction: column;
    overflow: hidden;
  }

  .wordblock {
    width: 80vw;
    height: 100vw;
    overflow: hidden;
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .imgblock {
    width: 100vw;
    height: 100vw;
    overflow: hidden;
  }
  .words {
    width: 100%;
    height: fit-content;
    padding-bottom: 15vh;
    align-items: left;
    overflow: hidden;
  }
  .hear {
    font-size: 5vw;
  }
  .stayc {
    padding-top: 5vw;
  }
  .contactmobilefit {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 10vw;
  }
}
